<template>
  <view class="change-password-container">
    <view class="change-password-card">
      <!-- Â§¥ÈÉ® -->
      <view class="header">
        <view class="header-icon">üîê</view>
        <view class="header-title">‰øÆÊîπÂØÜÁ†Å</view>
        <view class="header-subtitle">ËØ∑ËæìÂÖ•ÂéüÂØÜÁ†ÅÂíåÊñ∞ÂØÜÁ†Å</view>
      </view>

      <!-- ‰øÆÊîπÂØÜÁ†ÅË°®Âçï -->
      <view class="form-section">
        <view class="form-item">
          <view class="input-wrapper">
            <text class="input-icon">üîí</text>
            <input 
              class="form-input" 
              type="password" 
              v-model="formData.currentPassword"
              placeholder="ËØ∑ËæìÂÖ•ÂΩìÂâçÂØÜÁ†Å"
              @input="clearError"
            />
          </view>
        </view>

        <view class="form-item">
          <view class="input-wrapper">
            <text class="input-icon">üÜï</text>
            <input 
              class="form-input" 
              type="password" 
              v-model="formData.newPassword"
              placeholder="ËØ∑ËæìÂÖ•Êñ∞ÂØÜÁ†Å"
              @input="clearError"
            />
          </view>
        </view>

        <view class="form-item">
          <view class="input-wrapper">
            <text class="input-icon">‚úÖ</text>
            <input 
              class="form-input" 
              type="password" 
              v-model="formData.confirmPassword"
              placeholder="ËØ∑ÂÜçÊ¨°ËæìÂÖ•Êñ∞ÂØÜÁ†Å"
              @input="clearError"
            />
          </view>
        </view>

        <!-- ÈîôËØØÊèêÁ§∫ -->
        <view class="error-message" v-if="errorMessage">
          <text class="error-text">{{ errorMessage }}</text>
        </view>

        <!-- ÂØÜÁ†ÅË¶ÅÊ±ÇÊèêÁ§∫ -->
        <view class="password-tips">
          <view class="tips-title">ÂØÜÁ†ÅË¶ÅÊ±ÇÔºö</view>
          <view class="tips-item">‚Ä¢ ÈïøÂ∫¶Ëá≥Â∞ë6‰Ωç</view>
          <view class="tips-item">‚Ä¢ Âª∫ËÆÆÂåÖÂê´Â≠óÊØçÂíåÊï∞Â≠ó</view>
        </view>

        <!-- Êìç‰ΩúÊåâÈíÆ -->
        <view class="form-actions">
          <button 
            class="btn-cancel" 
            @click="handleCancel"
          >
            ÂèñÊ∂à
          </button>
          <button 
            class="btn-save" 
            :class="{ loading: isLoading }"
            @click="handleChangePassword"
            :disabled="isLoading"
          >
            <text v-if="!isLoading">Á°ÆËÆ§‰øÆÊîπ</text>
            <text v-else>‰øÆÊîπ‰∏≠...</text>
          </button>
        </view>
      </view>
    </view>
  </view>
</template>

<script>
export default {
  data() {
    return {
      formData: {
        currentPassword: '',
        newPassword: '',
        confirmPassword: ''
      },
      errorMessage: '',
      isLoading: false
    }
  },

  onLoad() {
    // Ê£ÄÊü•ÁôªÂΩïÁä∂ÊÄÅ
    this.checkAuth();
  },

  methods: {
    // Ê£ÄÊü•ËÆ§ËØÅÁä∂ÊÄÅ
    checkAuth() {
      const { checkPageAuth } = require('../../utils/auth.js');
      return checkPageAuth();
    },

    // Ê∏ÖÈô§ÈîôËØØ‰ø°ÊÅØ
    clearError() {
      this.errorMessage = '';
    },

    // Ë°®ÂçïÈ™åËØÅ
    validateForm() {
      if (!this.formData.currentPassword.trim()) {
        this.errorMessage = 'ËØ∑ËæìÂÖ•ÂΩìÂâçÂØÜÁ†Å';
        return false;
      }

      if (!this.formData.newPassword.trim()) {
        this.errorMessage = 'ËØ∑ËæìÂÖ•Êñ∞ÂØÜÁ†Å';
        return false;
      }

      if (this.formData.newPassword.length < 6) {
        this.errorMessage = 'Êñ∞ÂØÜÁ†ÅÈïøÂ∫¶Ëá≥Â∞ë6‰Ωç';
        return false;
      }

      if (!this.formData.confirmPassword.trim()) {
        this.errorMessage = 'ËØ∑Á°ÆËÆ§Êñ∞ÂØÜÁ†Å';
        return false;
      }

      if (this.formData.newPassword !== this.formData.confirmPassword) {
        this.errorMessage = '‰∏§Ê¨°ËæìÂÖ•ÁöÑÊñ∞ÂØÜÁ†Å‰∏ç‰∏ÄËá¥';
        return false;
      }

      if (this.formData.currentPassword === this.formData.newPassword) {
        this.errorMessage = 'Êñ∞ÂØÜÁ†Å‰∏çËÉΩ‰∏éÂΩìÂâçÂØÜÁ†ÅÁõ∏Âêå';
        return false;
      }

      return true;
    },

    // Â§ÑÁêÜ‰øÆÊîπÂØÜÁ†Å
    async handleChangePassword() {
      if (!this.validateForm()) return;

      this.isLoading = true;
      this.errorMessage = '';

      try {
        // È™åËØÅÂΩìÂâçÂØÜÁ†ÅÂπ∂‰øÆÊîπ
        const result = await this.changePassword(
          this.formData.currentPassword,
          this.formData.newPassword
        );

        if (result.success) {
          // ‰øÆÊîπÊàêÂäü
          uni.showToast({
            title: 'ÂØÜÁ†Å‰øÆÊîπÊàêÂäü',
            icon: 'success',
            duration: 2000
          });

          // Âª∂ËøüËøîÂõû‰∏ä‰∏ÄÈ°µ
          setTimeout(() => {
            uni.navigateBack();
          }, 2000);
        } else {
          this.errorMessage = result.message || 'ÂØÜÁ†Å‰øÆÊîπÂ§±Ë¥•';
        }
      } catch (error) {
        console.error('‰øÆÊîπÂØÜÁ†ÅÂ§±Ë¥•:', error);
        this.errorMessage = '‰øÆÊîπËøáÁ®ã‰∏≠ÂèëÁîüÈîôËØØÔºåËØ∑ÈáçËØï';
      } finally {
        this.isLoading = false;
      }
    },

    // ‰øÆÊîπÂØÜÁ†ÅÈÄªËæë
    async changePassword(currentPassword, newPassword) {
      try {
        // Ëé∑ÂèñÂΩìÂâçÁî®Êà∑‰ø°ÊÅØÂíåÂØÜÁ†Å‰øÆÊîπÊñπÊ≥ï
        const { getCurrentUser, changeUserPassword } = require('../../utils/auth.js');
        const currentUser = getCurrentUser();
        
        if (!currentUser) {
          return { success: false, message: 'Áî®Êà∑‰ø°ÊÅØ‰∏çÂ≠òÂú®ÔºåËØ∑ÈáçÊñ∞ÁôªÂΩï' };
        }

        // Ë∞ÉÁî®authÊ®°ÂùóÁöÑÂØÜÁ†Å‰øÆÊîπÊñπÊ≥ï
        const result = changeUserPassword(currentUser.username, currentPassword, newPassword);
        
        return result;
      } catch (error) {
        console.error('‰øÆÊîπÂØÜÁ†ÅËøáÁ®ã‰∏≠Âá∫Èîô:', error);
        return { success: false, message: 'Á≥ªÁªüÈîôËØØÔºåËØ∑Á®çÂêéÈáçËØï' };
      }
    },

    // ÂèñÊ∂à‰øÆÊîπ
    handleCancel() {
      if (this.formData.currentPassword || this.formData.newPassword || this.formData.confirmPassword) {
        uni.showModal({
          title: 'Á°ÆËÆ§ÂèñÊ∂à',
          content: 'Á°ÆÂÆöË¶ÅÂèñÊ∂à‰øÆÊîπÂØÜÁ†ÅÂêóÔºüÂ∑≤ËæìÂÖ•ÁöÑÂÜÖÂÆπÂ∞Ü‰∏¢Â§±„ÄÇ',
          success: (res) => {
            if (res.confirm) {
              uni.navigateBack();
            }
          }
        });
      } else {
        uni.navigateBack();
      }
    }
  }
}
</script>

<style>
.change-password-container {
  min-height: 100vh;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 40rpx;
}

.change-password-card {
  width: 100%;
  max-width: 600rpx;
  background: white;
  border-radius: 24rpx;
  padding: 0;
  box-shadow: 0 20rpx 60rpx rgba(0, 0, 0, 0.1);
  overflow: hidden;
}

.header {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  padding: 60rpx 40rpx 40rpx;
  text-align: center;
  color: white;
}

.header-icon {
  font-size: 80rpx;
  margin-bottom: 20rpx;
}

.header-title {
  font-size: 48rpx;
  font-weight: bold;
  margin-bottom: 12rpx;
}

.header-subtitle {
  font-size: 28rpx;
  opacity: 0.9;
}

.form-section {
  padding: 40rpx;
}

.form-item {
  margin-bottom: 32rpx;
}

.input-wrapper {
  display: flex;
  align-items: center;
  background: #f8f9fa;
  border-radius: 12rpx;
  padding: 0 20rpx;
  border: 2rpx solid transparent;
  transition: all 0.3s;
}

.input-wrapper:focus-within {
  border-color: #667eea;
  background: white;
  box-shadow: 0 0 0 4rpx rgba(102, 126, 234, 0.1);
}

.input-icon {
  font-size: 32rpx;
  margin-right: 16rpx;
  color: #999;
}

.form-input {
  flex: 1;
  height: 88rpx;
  font-size: 32rpx;
  color: #333;
  background: transparent;
  border: none;
}

.form-input::placeholder {
  color: #999;
}

.error-message {
  margin-bottom: 24rpx;
  padding: 16rpx 20rpx;
  background: #ffeaea;
  border-radius: 8rpx;
  border-left: 6rpx solid #ff4757;
}

.error-text {
  font-size: 26rpx;
  color: #ff4757;
}

.password-tips {
  background: #f0f7ff;
  border-radius: 12rpx;
  padding: 24rpx;
  margin-bottom: 40rpx;
  border: 1rpx solid #e1f0ff;
}

.tips-title {
  font-size: 28rpx;
  font-weight: bold;
  color: #1890ff;
  margin-bottom: 12rpx;
}

.tips-item {
  font-size: 24rpx;
  color: #666;
  margin-bottom: 8rpx;
  line-height: 1.4;
}

.form-actions {
  display: flex;
  gap: 20rpx;
}

.btn-cancel, .btn-save {
  flex: 1;
  height: 88rpx;
  border: none;
  border-radius: 12rpx;
  font-size: 32rpx;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s;
}

.btn-cancel {
  background: #f5f5f5;
  color: #666;
}

.btn-cancel:active {
  background: #e8e8e8;
}

.btn-save {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
}

.btn-save:not([disabled]):active {
  transform: scale(0.98);
}

.btn-save.loading {
  opacity: 0.8;
}

.btn-save[disabled] {
  opacity: 0.6;
}
</style>